/* unplugin-vue-components disabled */import { computed, nextTick, ref, watch } from 'vue';
import { setWorldChatApi } from '@/api/function';
import { useStore } from 'vuex';
import { useRoute } from 'vue-router';
export default {
  name: 'Chat',

  setup() {
    //当前的频道
    let channel = ref('shijie'); //存储世界聊天
    // let shijieChatBox = ref([])

    let shijieChatBox = computed(() => {
      return store.state.user.worldChatBox;
    }); //存储自己门派聊天
    // let mymenpaiChatBox = ref([])

    let mymenpaiChatBox = computed(() => {
      return store.state.user.menpaiChatBox;
    }); //输入框内容

    let messageText = ref(null); //控制input框高度

    let autoSizeObj = {
      minRows: 1,
      maxRows: 2
    }; //获取末尾元素

    const endP = ref(null);
    const store = useStore(); //存储自己门派的id

    let myMenpaiId = computed(() => {
      return store.getters['user/getMenpaiId'];
    }); //获取输入框元素

    const elInput = ref(null);
    const elBtn = ref(null);
    const tabItem = ref(null); //监听 输入框内容

    watch(() => messageText.value, newVal => {
      nextTick(() => {
        //获取input的高度
        //如果输入的长度超过一半
        if (newVal.length >= 19) {
          elBtn.value.style.height = '8vh';
        } else {
          elBtn.value.style.height = '5vh';
        } //设置新的tabItem高度


        tabItem.value.style.height = `calc(100vh - 40px - 10vh - 30px - ${elBtn.value.style.height})`; //判断滚动条是否到底部 如果发生变化则获取滚动条底部 true为到底了

        let isBottom = tabItem.value.scrollHeight - tabItem.value.scrollTop === tabItem.value.clientHeight; // 如果输入框高度发生变化 或者 滚动条没有到最底部

        if (!isBottom) {
          //滚动条滚动到底部
          tabItem.value.scrollTop = tabItem.value.scrollHeight - tabItem.value.clientHeight; // 让滚动条滚动endP到视图位置
          // endP.value.scrollIntoView()
        }
      });
    }, {
      deep: true
    }); //切换频道

    const changeChannel = activeName => {
      //清空输入框内容
      messageText.value = ''; //如果点的是门派标签 且自己没门派 禁止跳转

      if (activeName === 'menpai' && myMenpaiId.value === '0') {
        ElMessage.error('您未加入门派!');
        return false;
      }
    };

    const route = useRoute(); //监听世界聊天和门派聊天盒子

    watch([shijieChatBox.value, mymenpaiChatBox.value], () => {
      //判断当前是否在聊天页面
      nextTick(() => {
        if (route.path === '/chat') {
          //判断滚动条是否到底部 如果发生变化则获取滚动条底部 true为到底了
          let isBottom = tabItem.value.scrollHeight - tabItem.value.scrollTop === tabItem.value.clientHeight;

          if (!isBottom) {
            //让滚动条滚动到底部位置
            tabItem.value.scrollTop = tabItem.value.scrollHeight - tabItem.value.clientHeight; //让滚动条滚动endP到视图位置
            // endP.value.scrollIntoView()
          }
        }
      });
    }, {
      immediate: true
    }); //点击发送按钮

    const sentMessageBtn = async () => {
      //替换逻辑
      let reg = new RegExp('%', 'g'); //g代表全部

      let reg2 = new RegExp('\\+', 'g'); //g代表全部
      //获取发送的聊天内容

      let sentMessage = messageText.value;
      sentMessage = sentMessage.trim();

      if (sentMessage === '我要补偿' || sentMessage === '领取补偿' || sentMessage === '控制台@') {
        ElMessage.info('手机无法使用此功能，请在电脑端尝试');
      } else {
        //因为是http提交，所以此处需要将符号 % 替换为 y9m0p 这串文字
        //将符号 + 替换为 v1sp 这串文字
        sentMessage = sentMessage.replace(reg, 'y9m0p');
        sentMessage = sentMessage.replace(reg2, 'v1sp');
        console.log(sentMessage);

        try {
          //调用接口发送请求
          let code = await setWorldChatApi(sentMessage);

          if (code !== 1) {
            code = code.substr(1, code.length);
            ElMessage.error(code);
          }
        } catch (error) {
          ElMessage.error('聊天网络错误');
          console.log(`聊天:${error.message}`);
        }
      } //清空输入框内容


      messageText.value = '';
    }; //输入框按下回车或失去焦点触发


    const inputSentMessage = async () => {
      //调用发送
      await sentMessageBtn();
    };

    return {
      channel,
      changeChannel,
      shijieChatBox,
      mymenpaiChatBox,
      endP,
      messageText,
      autoSizeObj,
      elInput,
      elBtn,
      tabItem,
      inputSentMessage,
      sentMessageBtn
    };
  }

};